<template>
  <div class="remenlist">
  <div id="slider" class="mui-slider">
				<div id="sliderSegmentedControl" class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
					<div class="mui-scroll">
						<a :class="['mui-control-item', types.id==1 ? 'mui-active' :'']" @click="btns(types.id)"  v-for="types in type" :key="types.id" >
							{{types.name}}
						</a>
					
					</div>
				</div>

			</div>
			<ul>
				<li v-if="remens.videouri" v-for="(remens,i) in remen" :key=i>
				<strong><video :src="[remens.videouri==''?remens.cdn_im:remens.videouri]"></video></strong>
				<span>{{remens.text}}</span>
				</li>
			</ul>
  </div>
</template>

<script>
import mui from '../../lib/mui/js/mui.min.js'


export default {
  name: 'remenlist',
  data () {
    return {
		 remen:1,
		 type:[{
			 id:1,
			 name:"全部"
		 },{
			 id:66,
			 name:"图片"
		 },{
			 id:4,
			 name:"视频"
		 },{
			 id:6,
			 name:"文字" 
		 },{
			 id:7,
			 name:"其他"
		 },
		 {
			 id:8,
			 name:"其他"
		 },]
    }
	},
	created(){
  this.getremenlist();
	},
  mounted(){
      mui('.mui-scroll-wrapper').scroll({
	deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
});
	},
	methods:{
		getremenlist(){
			this.$http.get("satinApi?type="+this.remen+"&page=0").then((result) => {
				
				if(result.body.code==200){
				 this.remen=result.body.data
		
				}
			}).catch((err) => {
				console.log(err)
			});
		},
		btns(id){
			this.remen=id;
			this.getremenlist();
		}
	}
}
</script>


<style lang="scss" scoped>
*{
	padding: 0;
	 margin: 0;

}

*{ touch-action:pan-y; }  
 ul li{
	 position: relative;
	 margin: 20px;
	 list-style: none;
	
	  strong{
		  display: block;
	
		
	 video{
		 display: inline-block;
		 width: 100%;
		 vertical-align: bottom;
		background:url("http://7xi8d6.com1.z0.glb.clouddn.com/20171102092251_AY0l4b_alrisaa_2_11_2017_9_22_44_335.jpeg");
		
	 }}
	 span{
		 display:inline-block;
		 padding: 10px 30px;
		 position: absolute;
		 bottom: 0px;
		 width: 100%;
		 max-height: 70px;
		 background: rgba(0,0,0,.4);
		 text-overflow: ellipsis;
		 -webkit-line-clamp:3;
		 color: white;
		
	 }
	
 }
</style>






