// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
//导入vue-resource
import VueResource from 'vue-resource'
import axios from 'axios'

Vue.prototype.$axios=axios
// axios.defaults.baseURL="http://api.apiopen.top/"

Vue.use(VueResource)
//导入图片预览插件
import VuePreview from 'vue-preview'
Vue.use(VuePreview)
Vue.http.options.root='https://www.apiopen.top/'

Vue.http.options.emulateJSON=true
import router from './router'
//导入mint-ui并且注册
import Mint from 'mint-ui'

import 'mint-ui/lib/style.css'
Vue.use(Mint)
import App from './App'
//引入jquery
//引入bootstr
import 'bootstrap/dist/css/bootstrap.css'
import './lib/mui/css/mui.css'
import './lib/mui/css/icons-extra.css'
import './css/app.css'


Vue.filter( 'totime',(el)=>{
  var dt=new Date(el);
 var n=dt.getFullYear();
  var y=(dt.getMonth()+1).toString().padStart(2,0);
  var r=dt.getDate().toString().padStart(2,0);
  var s=dt.getHours().toString().padStart(2,0);
  var f=dt.getMinutes().toString().padStart(2,0);
  var m=dt.getSeconds().toString().padStart(2,0);
  return `${n}-${y}-${r} ${s}:${f}:${m}`
})
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



