<template>
  <div class="meitu" >
    
  <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="isAutoFill">
  <ul>
    <router-link :to="'/home/presons/'+id" tag="li" v-for="item in list" :key="item.url">
      <img v-lazy="item.url">
    </router-link>
  </ul>
</mt-loadmore>
  </div>
</template>

<script>

export default {
  name: 'meitu',
  data () {
    return {
     id:1,
     list:[],
     allLoaded:false,//false为上啦加载时提示（释放更新）如果为true那么不能上啦
     isAutoFill:false
    }
  },
  created(){
      this.getmeitu();
  },
  methods:{
      getmeitu(){
          this.$http.get("meituApi?page="+this.id)
          .then(res=>{this.list=this.list.concat(res.body.data)})
          .catch(err=>{console.log(err)})
      },
      loadBottom() {
        this.id++;
        this.getmeitu();
     // load more data
    // if all data are loaded
      this.$refs.loadmore.onBottomLoaded();
                
                },
  
  },
  mounted(){
    
  }
}
 
</script>


<style lang="scss" scoped>
*{
  margin: 0;
  padding: 0;
}
.meitu{
  overflow: scroll;
  height: 640px;
}

ul li{
  list-style: none;
  padding: 30px;
  background:rgba(231, 227, 227, 0.3);
  margin: 10px 0;
  text-align: center;
  
  img{
    width: 100%;
    height: 100%;
    text-align:center;
     margin:0 auto;
  }
  img[lazy=loading] {
  width: 40px;
  height: 300px;
 
  text-align: center;
}

}
</style>






