<template>
  <div class="newslist">
  <ul class="mui-table-view">
				<li class="mui-table-view-cell mui-media" v-for="itmes in news" :key="itmes.avatar_url">
					<router-link :to="'/home/newsinfo/'+itmes.docid">
						<span id="img" class="mui-media-object mui-pull-left" v-img></span>
						<div class="mui-media-body">
						    <h4  v-text="itmes.title"></h4>
							<p class='mui-ellipsis'>
                            <span>时间：{{itmes.ptime | totime}}</span><span>{{itmes.source}}</span>
                            </p>
						</div>
					</router-link>
				</li>
			</ul>
  </div>
</template>

<script>

import {Toast} from 'mint-ui'
export default {
  name: 'newslist',
  data () {
    return {
     news:[]
    }
  },
  created(){
   this.getnewlist();
  
  },
  methods:{
   getnewlist(){
       this.$http.get("journalismApi").then(res=>{
           if(res.body.code==200){
             const {tech,auto,money,sports,dy,war,ent,toutiao} =res.body.data
             this.news=[...tech,...auto,...money,...sports,...dy,...war,...ent,...toutiao]
           }else{
           Toast({
               message:"请求数据超时！请稍后浏览",
                position: 'center',
                duration: 6000
           })
           }
       })
       .catch(err=>{console.log(err)})
   }
  },
  
  directives:{
      'img':{
          inserted:function(el){
              el.style.backgroundColor="#"+Math.random().toString(16).slice(2,8);
          }
      }
  }
  
}
</script>


<style lang="scss" scoped>
li{
    
  h4{
      overflow: hidden;
     width: 100%;
     white-space: nowrap;
     text-overflow:ellipsis;

 }
 .mui-ellipsis{
     display: flex;
     justify-content: space-between;
     color: blue;
 }
 #img{
     display: inline-block;
     background: #000;
      
     width: 100%;
    
     text-overflow:ellipsis;
     color: #fff;
     font-size: 12px;
 }
}
</style>






