<template>
  <div id="app">
  <mt-header fixed title="陈凯.product" class="headerss">
    <router-link to="/" slot="left">
    <mt-button v-show="falg" @click="back()" class="text" icon="back">返回</mt-button>
  </router-link>
  </mt-header>




  
<transition>
 <router-view></router-view>
</transition>
  
  
  
  <nav class="mui-bar mui-bar-tab">
			<router-link  class="mui-tab-item1" to="/home">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</router-link>
			<router-link class="mui-tab-item1" to="/member">
			<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">会员</span>
			</router-link>
			<router-link class="mui-tab-item1" to="/shop">
			<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</router-link>
			<router-link class="mui-tab-item1" to="/search">
				<span class="mui-icon mui-icon-search"></span>
				<span class="mui-tab-label">搜索</span>
			</router-link>
		</nav>
 
  </div>

</template>

<script>


export default {
  name: 'App',
 data(){
   return{
    hh:{name:"天灾",content:"我看见的健康程度看",sex:"男"},
    falg:false
	 }
	 
 },
 
 created(){{
    // this.$http.post("http://localhost:8080/static/db.json/getcomentlist",this.hh).then(res=>
    // console.log(res);
    this.falg=this.$route.path=='/home'?false:true
	}
 },
 methods:{
    back(){
          this.$router.go(-1)
    },
    
  },
  watch:{
     '$route.path':function(newVal){
       if(newVal==='/home'){
         this.falg=false
       }else{
         this.falg=true
       }
     }
    }
  
  
 
  
}
</script>

<style lang="scss" scoped>

#app{
	padding-top: 40px;
	padding-bottom: 40px;
	overflow-x: hidden;
}
.v-enter
{
opacity: 0;
transform: translateX(100%);
}
.v-leave-to{
	transform: translateX(-100%);
	position: absolute;
}
.v-enter-active,
.v-leave-active{
  transition:all 1s ease;
}

	.mui-bar-tab .mui-tab-item1 {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item1.mui-active {
    color: #007aff;
}
.mui-bar-tab .mui-tab-item1 .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}

.mui-bar-tab .mui-tab-item1 .mui-icon ~ .mui-tab-label {
    font-size: 11px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}
.headerss{
	z-index: 3;
  .text{
    line-height: 40px;
  }
}

</style>







