<template>
  <div class="satinlist">
   <ul class="mui-table-view">
				<li class="mui-table-view-cell mui-media" v-for="items in satin" :key="items.id">
					<a href="javascript:;">
						<img v-lazy="items.user.profile_image" class="mui-media-object mui-pull-left">
            <span>{{items.user.username}}（用户）</span>
						<div class="mui-media-body">
							<p class="mui-ellipsis">说于：{{items.ctime | totime }}</p>
							{{items.content}}
						</div>
					</a>
				</li>
		</ul>
   <mt-button type="danger" size="large" plain @click="btns">加载更多</mt-button>
  </div>
</template>

<script>
import {Toast} from 'mint-ui'
export default {
  name: 'satinlist',
  data () {
    return {
      page:1,
      satin:[]
    }
  },
  created(){
    this.getsatinlist();
  },
  methods:{
      getsatinlist(){
        this.$http.get("satinCommentApi?id=27610708&page="+this.page).then(res=>{
          if(res.body.code==200){
        this.satin=this.satin.concat(res.body.data.normal.list)
                      }else{
                        Toast({
                          message:"数据加载失败",
                           position: 'center',
                          duration: 6000
                        })
                      } 
       })
       .catch(err=>{console.log(err)})
        
      },
      btns(){
         this.page++;
         this.getsatinlist();
      }
    },
   
}
</script>


<style lang="scss" scoped>
img[lazy=loading] {
  width: 40px;
  height: 40px;
  margin: auto;
}
.satinlist{
  padding: 0 0 10px 0; 
}
</style>






