<template>
  <div class="newsinfo">
    <div class="heads">
    <h4>{{info.title}}</h4>
    <p>
        <span>发表时间{{info.ptime}}</span><span>{{info.source}}</span>
    </p>
    <hr>
    </div>
    
    <div class="content">
      <a :href="info.link">{{info.link}}</a>
      <span>{{info.link}}</span>
      <span>{{info.link}}</span>
      <span>{{info.link}}</span>
    </div>
  </div>
</template>

<script>
import {Toast} from 'mint-ui'

export default {
  name: 'newsinfo',
  data () {
    return {
     docid:this.$route.params.docid,
     info:""
    }
  },
  created(){
 this.getnewsinfo();
  },
  methods:{
      getnewsinfo(){

          this.$http.get("journalismApi")
          .then(res=>{
              if(res.body.code==200){
                   const {tech,auto,money,sports,dy,war,ent,toutiao} =res.body.data
                   this.info=[...tech,...auto,...money,...sports,...dy,...war,...ent,...toutiao].find((data)=>{
                   return data.docid==this.docid
            })}else{
              Toast({
                  message:"获取数据失败",
                   position: 'center',
                duration: 6000
              })
            }
           
          })
          .catch(err=>{console.log(err)})
      },
    

  }
}
</script>


<style lang="scss">

.newsinfo{
    *{
    padding: 0;
    margin: 0;
}
  
.heads{
    background: #ecebeb;
h4{
    width: 100%;
    text-align: center;
    padding: 8px 4px;
    color: red;
    font-weight: bold;
}
p{
    display: flex;
    justify-content: space-between;
    width: 100%;
    color: blue;
    padding: 4px;
    
}
hr{
    font-weight: bold;
    background: red;
}
}
.content{
    padding: 0 4px;
    width: 100%;
    
   span{
    display:block;
     overflow: hidden;
     width: 100%;
     white-space:inherit;
     word-break: break-all;
   
   }
}
.xiaoshuo{
    span{
        text-decoration: underline;
        color: black;
    }
}

}
</style>






