<template>
  <div class="home">
 <mt-swipe :auto="4000">
  <mt-swipe-item v-for="itmes in tu" :key="itmes.url">
      <img :src="itmes.url">
  </mt-swipe-item>
</mt-swipe>
<ul class="mui-table-view mui-grid-view mui-grid-9">
		            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/home/newslist">
		                    <img src="../assets/menu1.png">
		                    <div class="mui-media-body">新闻资讯</div></router-link></li>
		            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/home/satinlist">
		                    <img src="../assets/menu2.png">
		                    <div class="mui-media-body">热门段子</div></router-link></li>
		            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/home/remlist">
		                    <img src="../assets/menu3.png">
		                    <div class="mui-media-body">商品购买</div></router-link></li>
		            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/home/musics">
		                  <img src="../assets/menu4.png">
		                    <div class="mui-media-body">音乐</div></router-link></li>
		            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/home/meitu">
		                   <img src="../assets/menu5.png">
		                    <div class="mui-media-body">美图专区</div></router-link></li>
		            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/home">
		                    <img src="../assets/menu6.png">
		                    <div class="mui-media-body">联系我们</div></router-link></li>
		        </ul> 
               
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'mint-ui';
export default {
  name: 'home',
  data () {
    return {
     tu:""
    }
  },
  created(){
 this.getlunbotu();
  },
  methods:{
      getlunbotu(){
          this.$http.get("meituApi?page=1")
          .then(res=>{
            if(res.body.code==200){
              
                this.tu=res.body.data.splice(Math.floor(Math.random()*(res.body.data.length-6)),6);
            }
              })
      }
  }
}
</script>


<style lang="scss" scoped>
*{ touch-action:pan-y; } 
.mint-swipe{
    height: 200px;
    .mint-swipe-item{
        &:nth-child(1){
          background-color: red;
          }
           &:nth-child(2){
          background-color: blue;
          }
           &:nth-child(3){
          background-color: black;
          }
          img{
              width: 100%;
            height: 100%;
          }
    }
}
.mui-table-view{
    background-color: #fff;
    .mui-table-view-cell{
        border:none;
        img{
            width: 60px;
              height: 60px;
          
        }
    }
}
</style>


