import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/home'
import Shop from '@/components/shop'
import Member from '@/components/member'
import Search from '@/components/search'
import NewsList from '@/components/news/newslist'
import NewsInfo from '@/components/news/newsinfo'
import SatinList from '@/components/satins/satinlist'
import RemenList from '@/components/rems/remenlist'
import MeiTu from '@/components/tu/meitu'
import PreSon from '@/components/tu/preson'
import Musics from '@/components/music/musics'

Vue.use(Router)

export default new Router({
 
  routes: [
    {path: '/home', name: 'home',component: Home },
    {path: '/home/newslist',name: 'newlist',component: NewsList},
    {path: '/home/newsinfo/:docid',name: 'newinfor',component: NewsInfo},
    {path: '/',redirect:'/home'},
    {path: '/search',name: 'search',component: Search},
    {path: '/shop',name: 'shop',component: Shop},
    {path: '/member',name: 'member',component: Member},
    {path:'/home/satinlist',name:'satinlist',component:SatinList},
    {path:'/home/remlist',name:'remlist',component:RemenList},
    {path: '/home/meitu',name:'meitu',component:MeiTu},
    {path:'/home/presons/:id',name:'presons',component:PreSon},
    {path:'/home/musics',name:'musiclink',component:Musics}
  ],
  linkActiveClass:'mui-active'//覆盖默认的类（router-ling-active）
})



